Rendxx = Rendxx || {};
Rendxx.Game = Rendxx.Game || {};

(function (Game) {
    var TYPE = {
        'SERVER': 1,
        'HOST': 2,
        'CLIENT': 3,
        1: 'SERVER',
        2: 'HOST',
        3: 'CLIENT'
    };

    var TARGET = {
        'SERVER': 'SERVER',
        'HOST': 'HOST'
    };

    var CODE = {
        SERVER:{
            'NO_CODE': 0,
            'HOST_RESET_REQUEST' : 1,
            'HOST_RESET' : 2,
            'CLIENT_RESET_REQUEST' : 3,
            'CLIENT_RESET' : 4,
            'OB_RESET_REQUEST' : 5,
            'OB_RESET' : 6,
            'HOST_SETUPED' : 7,
            'HOST_UPDATE_CLIENTLIST' : 8,
            'HOST_UPDATE_OBLIST' : 9,
            'CACHE_GAME_SETUP' : 10,
            'CACHE_GAME' : 11,
            'GAME_START' : 12,
            'GAME_END' : 13,
            'GAME_RENEW': 14,
            'GAME_PAUSE': 15,
            'GAME_CONTINUE': 16,
            'GAME_SETUP': 17,
            'GAME_UPDATE': 18,
            'CACHE_OB': 18
        },
        HOST: {
            'NO_CODE': 0,
            'CLIENT_SETUP': 1,
            'CLIENT_UPDATE': 2,
            'ACTION': 3
        },
        CUSTOMIZED: 'CUSTOMIZED'
    };

    var timeStamp = function () {
        return Date.now() + "";
    };

    var messageDecode = function (msgStr) {
        var msg = {};
        try {
            var tmp = msgStr.split('|');
            msg['type'] = TYPE[Number(tmp[0])];
            msg['timestamp'] = tmp[1];
            msg['source'] = tmp[2];
            msg['code'] = Number(tmp[3]);
            msg['content'] = $.parseJSON(tmp[4]);
            return msg;
        } catch (e) {
            throw new Error("Illegal command");
        }
    };

    var messageEncode = function (msg) {
        if (msg == null || msg.type == null || msg.code == null) return;
        var d1 = Game.Message.DELIMITER.PART,
            d2 = Game.Message.DELIMITER.TARGET;
        return msg['type'] + d1
            + ('timestamp' in msg ? msg['timestamp'] : timeStamp()) + d1
            + (('target' in msg && msg['target'].length > 0) ? msg['target'].join(d2) : '') + d1
            + msg['code'] + d1
            + ('content' in msg ? msg['content'] : '');
    };

    // create JS hack
    if (!Date.now) {
        Date.now = function () { return new Date().getTime(); }
    }

    Game.Message = {
        TYPE: TYPE,
        TARGET: TARGET,
        CODE: CODE,
        DELIMITER: {
            PART: '|',
            TARGET: ','
        },
        Decode: messageDecode,
        Encode: messageEncode
    };
})(Rendxx.Game);